import React, { useEffect } from "react";
import "./App.css";




function runAnimation() {
  const canvas = document.getElementById("canvas1");
  const ctx = canvas.getContext("2d");
  canvas.width = window.innerWidth;
  canvas.height = window.innerHeight;

  let gradient = ctx.createLinearGradient(0, 0, canvas.width, canvas.height);
  gradient.addColorStop(0, "red");
  gradient.addColorStop(0.2, "orange");
  gradient.addColorStop(0.4, "yellow");
  gradient.addColorStop(0.6, "green");
  gradient.addColorStop(0.8, "blue");
  gradient.addColorStop(0.9, "cyan");
  gradient.addColorStop(1, "magenta");

  class Symbol {
    constructor(x, y, fontSize, canvasHeight) {
      this.characters =
        "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ";
      this.x = x;
      this.y = y;
      this.fontSize = fontSize;
      this.text = "";
      this.canvasHeight = canvasHeight;
    }

    draw(context) {
      this.text = this.characters.charAt(
        Math.floor(Math.random() * this.characters.length)
      );
      context.fillText(
        this.text,
        this.x * this.fontSize,
        this.y * this.fontSize
      );
      if (this.y * this.fontSize > this.canvasHeight && Math.random() > 0.98) {
        this.y = 0;
      } else {
        this.y += 1;
      }
    }
  }

  class Effect {
    constructor(canvasWidth, canvasHeight) {
      this.canvasWidth = canvasWidth;
      this.canvasHeight = canvasHeight;
      this.fontSize = 25;
      this.columns = this.canvasWidth / this.fontSize;
      this.symbols = [];
      this.#initialize();
    }
    #initialize() {
      for (let i = 0; i < this.columns; i++) {
        this.symbols[i] = new Symbol(i, 0, this.fontSize, this.canvasHeight);
      }
    }
  }

  const effect = new Effect(canvas.width, canvas.height);
  let lastTime = 0;
  const fps = 80;
  const nextFrame = 1000 / fps;
  let timer = 0;

  function animate(timeStamp) {
    const deltaTime = timeStamp - lastTime;
    lastTime = timeStamp;
    if (timer > nextFrame) {
      ctx.fillStyle = "rgba(0,0,0,0.05)";
      ctx.textAlign = "center";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      // ctx.fillStyle = "#0aff0a";
      ctx.fillStyle = gradient;
      ctx.font = effect.fontSize + "px monospace";
      effect.symbols.forEach((symbol) => symbol.draw(ctx));
      timer = 0;
    } else {
      timer += deltaTime;
    }

    requestAnimationFrame(animate);
  }
  animate(0);
}

function App() {
  const [value, setValue] = React.useState("");
  

  useEffect(() => {
    runAnimation();
  }, []);

  return (
    <>
      
       
        <div className="container">
          <div className="calculator">
            <form action="">
              <div className="display">
                <input type="text" value={value}/>
              </div>
              <div>
                <input type="button" value="AC" onClick={
                  () => setValue("")
                }/>
                <input type="button" value="DE" onClick={
                  () => setValue(value.substring(0, value.length - 1))
                }/>
                <input type="button" value="." onClick={
                  () => setValue(value + ".")
                }/>
                <input type="button" value="/" onClick={
                  () => setValue(value + "/")
                } />
              </div>

              <div>
                <input type="button" value="7" onClick={
                  () => setValue(value + "7") 
                }/>
                <input type="button" value="8" onClick={
                  () => setValue(value + "8")
                } />
                <input type="button" value="9" onClick={
                  () => setValue(value + "9")
                } />
                <input type="button" value="*" onClick={
                  () => setValue(value + "*")
                }/>
              </div>

              <div>
                <input type="button" value="4" onClick={
                  () => setValue(value + "4")
                }/>
                <input type="button" value="5" onClick={
                  () => setValue(value + "5")
                }/>
                <input type="button" value="6" onClick={
                  () => setValue(value + "6")
                } />
                <input type="button" value="+" onClick={
                  () => setValue(value + "+")
                }/>
              </div>

              <div>
                <input type="button" value="1" onClick={
                  () => setValue(value + "1")
                }/>
                <input type="button" value="2" onClick={
                  () => setValue(value + "2")
                }/>
                <input type="button" value="3" onClick={
                  () => setValue(value + "3")
                } />
                <input type="button" value="-" onClick={
                  () => setValue(value + "-")
                } />
              </div>

              <div>
                <input type="button" value="00" onClick={
                  () => setValue(value + "00")
                } />
                <input type="button" value="0" onClick={
                  () => setValue(value + "0")
                }/>
                <input type="button" value="=" className="equal" onClick={
                e => setValue(eval(value))
              


                 
                 
          
                }/>
              </div>
            </form>
          </div>
          <canvas id="canvas1"></canvas>
        </div>
   
    </>
  );
}

export default App;
